@import url(https://fonts.googleapis.com/css?family=Crimson+Text|Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  background-color: rgb(241, 241, 241);
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
    bottom:0;
    width: 100vw;
    /* height: 57px; */
}

.footer-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    width: 100vw;
    list-style-type: none;
    height: 57px;
    background-color: rgb(241, 241, 241);
    box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.295);
}

.footer-branding {
    padding-left: 20px;
}

.footer-cpr-li {
    -webkit-transform: translate(-50px);
            transform: translate(-50px)
}

.social-media-footer {
    margin: 0 auto;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    width: 80%;
    justify-content: space-around;
    padding: 0;
    padding-top: 14px;
}

.social-media-footer a {
    color: black;
}

.social-media-footer li {
    padding: 0;
    margin: 0;
}

.aside-data-footer {
    list-style-type: none;
    padding-left: 20px;
}

.mobile-only p {
    padding: 0;
    font-size: 0.9rem;
}

@media (max-width: 768px) {

    .footer-list {
        box-shadow: none;
    }

    .footer {
        display: inline-block;
        box-shadow: 0px -3px 5px rgba(255, 255, 255, 0.932);
        margin-top: 30px;
    }

    .footer-branding, 
    .footer-cpr {
        font-size: 0.8rem;
        padding: 0;
        margin: 2px;
    }

    .footer-cpr-li {
        -webkit-transform: translate(0);
                transform: translate(0)
    }

    .footer-list {
        padding-top: 6px;
        display: block;
        margin-bottom: 10px;
    }

    .footer-list li {
        padding-left: 20px;
    }

    .footer-branding {
        padding-left: 0px;
    }
}

@media (min-width: 769px) {
    .mobile-only {
        display: none;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Navbar */

.navbar-branding-mobile {
  margin-top: 12px;
  padding-left: 12px;
  font-family: 'Crimson Text', serif;
  font-size: 1.4rem;
}

.navbar {
  background-color: rgb(218, 218, 218);
  position: fixed;
  top: 0;
  width: 100vw;
  height: 57px;
  z-index: 1;
}

.navbar-list {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 50vw;
  justify-content: space-around;
  right: -33vw;
  list-style-type: none;
  height: 57px;
}

.navbar-list-item {
  text-align: center;
  width: 100px;
}

.navbar-link {
  display: inline-block;
  width: 100px;
  text-decoration: none;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 5px 0;
}

.navbar-link:hover {
  background-color: rgb(185, 185, 185);
  font-size: 1.1rem;
  margin-top: -2px;
}


.navbar {
  background-color: rgb(241, 241, 241);
  box-shadow: -3px 0 5px rgba(0, 0, 0, 0.295);
  position: fixed;
  top: 0;
  width: 100vw;
  height: 57px;
  z-index: 1;
}

/* FOOTER */

.page-container {
  position: relative;
  min-height: 100vh;
  margin-top: -57px;
}

@media (max-width: 768px) {
  .navbar-list {
    display: none;
  }
}

  @media (min-width: 769px) {
    .navbar-branding-mobile {
        display: none;
    }
}
.aside {
    position: fixed;
    text-align: center;
    width: 20vw;
    min-width: 289px;
    height: 100vh;
    margin-top: 57px;
    padding-top: 60px;
    box-shadow: 142px -20px 0 -140px rgba(0, 0, 0, 0.3);
    margin-bottom: 100px;
    z-index: 1;
}

.aside h1 {
    font-family: 'Crimson Text', serif;
    font-size: 2.5rem;
    letter-spacing: 2px;
}

.profile-photo {
    background: url(/portfolio/static/media/profile.4c79e875.jpg);
    width: 14vw;
    height: 14vw;
    margin: auto;
    background-size: cover;
    border-radius: 50%;
    margin-bottom: 30px;
}

.aside-data p {
    margin: 3px 0;
}

.aside-data {
    text-align: justify;
    font-size: 0.9rem;
    list-style: none;
    margin: 0 20px;
    padding-left: 0;
}

.social-media {
    padding: 0;
    margin: 3vh auto;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: space-around;
    width: 15vw;
}

.social-media li {
    padding: 0;
    margin: 0;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.social-media li:hover {
    font-size: 1.4rem;
    padding: 0 -4px;
}

.social-media li a {
    padding: 0;
    margin: 0;
    color: black;
    z-index: 1;
}

.fab {
    cursor: pointer;
}

/* MEDIA QUERYS */

@media (max-height: 538px) 
{
  .profile-photo {
    display: none;
   }
}

@media (max-width: 768px) {
    .aside {
        display: none;
    }
}
.toggle-button {
    position: fixed;
    height: 25px;
    width: 30px;
    background: transparent;
    display: flex;
    flex-direction: column;
    border: none;
    cursor: pointer;
    right: 16px;
    top: 16px;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    padding: 0;
    margin: 0;
    height: 3px;
    background-color: black;
}

@media (min-width: 769px) {
    .toggle-button {
        display:none;
    }
}
.side-drawer {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 70%;
    max-width: 400px;
    background-color: rgb(241, 241, 241);
    z-index: 15;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: -webkit-transform 0.4s ease-out;
    transition: -webkit-transform 0.4s ease-out;
    transition: transform 0.4s ease-out;
    transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.8rem 0;
    -webkit-transition: letter-spacing 0.4s;
    transition: letter-spacing 0.4s;
}

.side-drawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer li:hover, .side-drawer li:active {
    letter-spacing: 2px;
}

@media (min-width: 769px) {
    .side-drawer {
        display:none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 14;
    top: 0;
    left: 0;
}
