.project-tile {
    width: 22vw;
    margin-bottom: 20px;
    padding: 10px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.295);
}

.project-tile h2 {
    font-size: 1.3rem;
    padding-bottom: 0px;
    margin-bottom: 0px;
}

.project-tile p {
    padding-top: 0px;
    margin-top: 4px;
    text-align: justify;
}

.project-image {
    width: 100%;
    height: 20vh;
}

.project-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.project-url {
    padding: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.295);
    text-decoration: none;
    color: #000;
}

@media (max-width: 768px) {
    .project-tile {
        border-radius: 5px;
        width: 80%;
        padding: 0 20px;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.295), -3px -3px 5px white;
        padding-bottom: 10px;
        margin: 0 auto;
        margin-bottom: 20px;
    }

    .project-image {
        border-radius: 5px;
        margin: 10px auto;
        width: 80%;
        height: 150px;
        z-index: 1;
        border: 2px solid black;
    }

    .project-tile a {
        margin-bottom: 10px;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.295), -3px -3px 5px white;
    }

    .project-url {
        margin-left: 15px;
        padding: 10px;
        color: black;
        text-decoration: none;
        border-radius: 5px;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.295), -3px -3px 5px rgb(255, 255, 255);
    }
    
    .project-url:active {
        box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.295), 3px 3px 5px rgb(255, 255, 255);
    }
    
    .project-url:active,
    .project-url:focus {
        background-color: transparent;
        outline: none;
        color: black;
    }

    .project-tile h2 {
        font-size: 1.2rem;
    }

    .project-tile h2,
    .project-tile p {
        padding: 0 20px;
    }
    
}
